<template>
	<div class="gov-area-panel">
		<area-panel area-title="绿色认证统计" @hand-sub-title="handlePage">
			<div slot="panelMain" class="info-panel">
				<div class="area-section">
					<p class="info-title">认证地块面积（万亩）</p>
					<div class="info-container">
						<count-to :startVal="0" :endVal="landCertArea" :decimals="2" decimal="d" :multiLen="0" noBgStyle></count-to>
					</div>
				</div>
				<ul class="section-info">
					<li class="info-item">
						<p class="info-value">{{ landCertCount }} <span class="unit">个</span></p>
						<p class="info-des">认证地块数量</p>
					</li>
					<li class="info-item">
						<p class="info-value">{{ companyCertCount }} <span class="unit">家</span></p>
						<p class="info-des">认证经营主体数量</p>
					</li>
				</ul>
			</div>
		</area-panel>
	</div>
</template>

<script>
import areaPanel from '@/components/overview/area-panel';
import countTo from '@/components/common/countTo';
import {maxDecimalPlaces, areaLevelCorAreaId} from '@/common/fn';

export default {
	name: 'green-certification-9',
	data() {
		return {
			// 认证经营主体数量
			companyCertCount: 0,
			landCertArea: 0,
			landCertCount: 0
		};
	},
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	created() {
		this.initMoudle();
	},
	methods: {
		async initMoudle() {
			const res = await this.getGreenCertList();
			this.companyCertCount = res.companyCertCount;
			this.landCertArea = maxDecimalPlaces({value: res.landCertArea / 10000});
			this.landCertCount = res.landCertCount;
		},
		// 绿色认证统计
		getGreenCertList() {
			return this.$ajax
				.post(this.$api.govPatrol.statisticsByArea, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType
				})
				.then(({data}) => {
					if (data.respCode === 0) {
						return data.obj;
					}
				})
				.catch(e => {
					console.log(new Error('绿色认证统计error'), e);
				});
		},
		handlePage() {
			// this.$router.push({
			// 	name: 'greenCertList'
			// });
			this.$emit('handleClick', 'greenCertList', '绿色认证统计');
		}
	},
	computed: {},
	components: {
		areaPanel,
		countTo
	}
};
</script>

<style scoped lang="less">
.gov-area-panel {
	width: 100%;
	height: 100%;
	background-color: fade(#061f36, 34%);
	.info-panel {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		@containerW: 78.1%;
		.area-section {
			width: @containerW;
			margin: 0 auto;
			height: 35%;
			min-height: 100px;
			.info-title {
				font-size: 14px;
				color: #6dc1cb;
				line-height: 24px;
				min-height: 24px;
				text-align: center;
				height: 25%;
			}
			.info-container {
				width: 100%;
				margin-top: 15px;
				height: 55%;
				line-height: 55px;
				min-height: 55px;
				display: flex;
				align-items: center;
				justify-content: center;
				box-sizing: border-box;
				background: url('~@/assets/images/value-bg.png') no-repeat;
				background-size: 100% 100%;
				/deep/ .count-to {
					.octagon {
						width: 100%;
						padding: 0;
						&.single {
							._octagon {
								.iconfont {
									font-size: 28px;
									font-weight: 400;
									&.icon-number-1 {
										width: 25px;
									}
								}
							}
						}
					}
				}
			}
		}
		.section-info {
			width: @containerW;
			margin: 7% auto 0;
			display: flex;
			justify-content: space-between;
			.info-item {
				text-align: center;
				.info-value {
					font-size: 30px;
					color: #00f3fd;
					line-height: 45px;
					.unit {
						font-size: 18px;
						vertical-align: 0;
					}
				}
				.info-des {
					font-size: 14px;
					color: #6dc1cb;
					line-height: 24px;
				}
			}
		}
	}
}
@media screen and (max-width: 1530px) {
	.gov-area-panel .info-panel .section-info {
		width: 90%;
	}
}
</style>



